// React
import React from 'react';
import { motion, useScroll, useTransform } from 'framer-motion';
import { useLenis } from '@studio-freight/react-lenis';

// Component
import Cell from './Cell';
import Section from './Section';
import Text from './Text';

// Footer
const Footer = () => {

  // Parallax
  const { scrollYProgress } = useScroll();
  const yRange = useTransform(scrollYProgress, [0.75, 1], ['200%', '0%']);
  const oRange = useTransform(scrollYProgress, [0.875, 1], ['0', '1']);

  // Lenis Scroll To
  const lenis = useLenis()
  const top = () => {
    lenis.scrollTo(0);
  };

  // Component
  return (
    <Section id="footer">
      <motion.div className="cell -ac" style={{ y: yRange, opacity: oRange }}>
        <span>ShawnEJ</span>
        <span style={{ cursor: 'pointer' }} onClick={top}>2025</span>
        <Cell config="-rw -ac -gl">
          <Text type="a" href="https://www.instagram.com/rrryuda/">@rrryuda</Text>
          <Text type="a" href="https://www.instagram.com/nooonjng/">@nooonjng</Text>
        </Cell>
      </motion.div>
    </Section>
  );
}

export default Footer;