// React
import React from 'react';
import { motion, useScroll, useTransform } from 'framer-motion';

// Component
import Section from './Section';

// CSS
import './css/SectionCover.css';

// Img
import cover from './source/cover.png'; // 1:1 Ratio

// SectionCover
const SectionCover = () => {

  // Parallax
  const { scrollYProgress } = useScroll();
  const yRange = useTransform(scrollYProgress, [0, 0.375], ['0%', '-21.875%']);

  // Component
  return (
    <Section id="cover">
      <motion.img style={{ y: yRange }} src={cover} alt="Cover" />
    </Section>
  );
}

export default SectionCover;