// React
import React from 'react';

// CSS
import './css/Btn.css';

// Btn
const Btn = ({ config, content, src, alt, href }) => {
  const classes = config ? `button ${config}` : 'button';

  return (
    <a className={classes} href={href} target="__blank">
      <img src={src} alt={alt} />
      {content}
    </a>
  );
}

export default Btn; 